.input-box {
  margin: 1em 0;
  display: flex;
  align-items: center;
  justify-content: center;
  input {
    height: 40px;
    width: 60%;
    outline: 0;
    border-radius: 5px 0 0 5px;
    border: 0;
    padding: 0 16px;
  }
  button {
    height: 40px;
    width: 100px;
    background-color: #00ffa2;
    color: #000000;
    font-weight: bold;
    border: 0;
    cursor: pointer;
    transition: all 0.3s;
    border-radius: 0 5px 5px 0;
    &:hover {
      transform: scale(1.05);
    }
  }
}
